
const lang = JSON.parse(localStorage.getItem('lang'));  //lang.locale
const defaultLang = {
  locale: 'en-US'
}
const initialState = lang
  ? { status: { langSetted: true }, lang }
  : { status: { langSetted: false }, lang: defaultLang };

export const language = {
  namespaced: true,
  state: initialState,
  actions: {
    updateLang({commit}, lang) {
      commit('updateLang', lang);
    }
  },
  mutations: {
    updateLang(state, lang) {
      state.lang = lang;
      state.status.langSetted = true
    },
  }
};
