<template>
<b-navbar class="header" v-bind:shadow="shadow" v-bind:fixed-top="fixedTop">
        <template slot="brand">
            <a v-bind:href="'/'"><img class="jcent-logo" 
                    src="../assets/jcent.svg"
                ></a>
        
        </template>


        <template slot="end">

             <b-navbar-item v-if="locale=='en-US'"
               v-for="navLeftItem in navLeftItems" class="navbar-item" 
               v-bind:key="navLeftItem.item"
               v-bind:href="navLeftItem.route"
               target="_blank">
               {{ navLeftItem.item }}
             </b-navbar-item>



             <b-navbar-item v-if="locale=='zh-CN'"
               v-for="navLeftItem in navLeftItemsCN" class="navbar-item" 
               v-bind:key="navLeftItem.item"
               v-bind:href="navLeftItem.route"
               target="_blank">
               {{ navLeftItem.item }}
             </b-navbar-item>


          <b-navbar-item class="navbar-item" >
            <b-navbar-dropdown :label="languageLabel">
                <b-navbar-item @click="toggleLang('en')">
                    English
                </b-navbar-item>
                <b-navbar-item @click="toggleLang('zh')">
                    简体中文
                </b-navbar-item>
            </b-navbar-dropdown>
          </b-navbar-item>

            <b-navbar-item tag="div">
              <div class="buttons">
                <a class="button is-light unlock-button" @click="loginTronWeb()" v-if="!loggedIn">
                   {{ $t('login.signIn') }}
                </a>
                <a class="button is-light unlock-button" v-if="loggedIn">
                   {{ subAddress }}
                </a>
              </div>
            </b-navbar-item>

            
        </template>

    </b-navbar> 

</template>

<script>

  import china from './../assets/svg/flags/china.svg'
  import usa from './../assets/svg/flags/usa.svg'

  export default {
    data() {
      return {
        usa,
        china,
        shadow: false,
        fixedTop: true,
        active: false,
        navLeftItems: [{
            "item": "Stake",
            "route": "/stake"
          },
          {
            "item": 'Whitepaper',
            "route": "https://github.com/JCentNetwork/Documentation/blob/master/README.md"
          }],
        navLeftItemsCN: [{
            "item": "质押挖矿",
            "route": "/stake"
          },
          {
            "item": '白皮书',
            "route": "https://github.com/JCentNetwork/Documentation/blob/master/README_CN.md"
          }],
        isChinese: false,
        languageLabel: "English",
      }
    },
    created() {
      if (this.locale == 'zh-CN') {
        this.languageLabel = "简体中文"
      } else  {
        this.languageLabel = "English"
      }

    },
    computed: {
      locale() {
        return this.$store.state.language.lang.locale
      },
      loggedIn() {
        return this.$store.state.tronbalance.status.loggedIn
      },
      address() {
        return this.$store.state.tronbalance.tron.address
      },
      subAddress() {
        return this.address.slice(0, 4) + "..." + this.address.slice(-4)
      }
    },
    methods: {
      loginTronWeb() {
        this.$store.dispatch('tronweb/showLoginBox', true)
      },
      logoutTronWeb() {
        this.$store.dispatch('tronweb/showLoginBox', false)
      },
      toggleLang(lang) {

        var newLang = null

        if (lang == 'zh') {
          newLang = {
            locale: 'zh-CN'
          }
          //this.value = '0'
          this.isChinese = true
          this.languageLabel = '简体中文'

          //document.title = "Trust Domains - 可信去中心化金融生态"
        } else if (lang == 'en') {
          newLang = {
            locale: 'en-US'
          }
          //this.value = '1'
          this.isChinese = false
          this.languageLabel = 'English'

          ///document.title = "Trust Domains - For Trust Decentralized Finance Economy"
        }

        localStorage.setItem('lang', JSON.stringify(newLang))

        this.$i18n.locale = newLang.locale
        this.$store.dispatch('language/updateLang', newLang)
      },
      randomTheme: function() {
         this.selectedNavTheme = this.navbarThemes[Math.floor(Math.random() * this.navbarThemes.length)]
      }
    }
  }


</script>

