

const initialState =  { status: { loginBoxShow: false } }

export const tronweb = {
  namespaced: true,
  state: initialState,
  actions: {
    showLoginBox({commit}, isShow) {
      commit('showLoginBox', isShow);
    }
  },
  mutations: {
    showLoginBox(state, isShow) {
      state.status.loginBoxShow = isShow
    },
  }
};
