
const defaultTron = {
  address: '',
  balance: 0
}

const initialState =  { status: { loggedIn: false }, tron: defaultTron }

export const tronbalance = {
  namespaced: true,
  state: initialState,
  actions: {
    updateTronStatus({commit}, loggedIn) {
      commit('updateTronStatus', loggedIn);
    },
    updateTron({commit}, tronStatus) {
      commit('updateTron', tronStatus);
    }
  },
  mutations: {
    updateTronStatus(state, loggedIn) {
      state.status.loggedIn = loggedIn
    },
    updateTron(state, tronStatus) {
      state.tron = tronStatus
    }
  }
};
