  <div id="app">
    <app-navbar></app-navbar>
    <!-- Navbar end -->
    <router-view/>


    <app-footer></app-footer>
  </div>
</template>

<style lang="scss">
@import "./styles/index.scss";
</style>

<script>
import navbar from "@/components/Navbar"
//import social from "@/components/Social"
import footer from "@/components/Footer"

const TronWeb = require('tronweb')

export default {
  components: {
      "app-navbar": navbar,
      "app-footer": footer,
      //"app-social": social
  },
  mounted () {
    this.initLanguate()
    this.loadTronWeb()
  },
  methods: {
    initLanguate() {
      // init language
      this.$i18n.locale = this.$store.state.language.lang.locale

      if (this.$i18n.locale == 'zh-CN') {
        //document.title = "Trust Domains - 可信去中心化金融生态"
      } else if (this.$i18n.locale == 'en-US') {
        //document.title = "Trust Domains - For Trust Decentralized Finance Economy"
      }
    },
    loadTronWeb() {
      if (!window.tronWeb) {
        const HttpProvider = TronWeb.providers.HttpProvider;
        const fullNode = new HttpProvider('https://api.trongrid.io');
        const solidityNode = new HttpProvider('https://api.trongrid.io');
        const eventServer = 'https://api.trongrid.io/';
    
        const tronWeb = new TronWeb(
            fullNode,
            solidityNode,
            eventServer,
        );

        window.tronWeb = tronWeb;

      }
    },
    snackbarTrig() {
      this.$snackbar.open({
        duration: 5000,
        message: 'Snackbar with red action, positioned on bottom-left and a callback.',
        type: 'is-danger',
        position: 'is-top',
        actionText: 'Undo',
        queue: false,
        onAction: () => {
            this.$toast.open({
                message: 'Action pressed',
                queue: false
            })
        }
      })
    }
  }
}

document.addEventListener('DOMContentLoaded', () => {

  // Get all "navbar-burger" elements
  const $navbarBurgers = Array.prototype.slice.call(document.querySelectorAll('.navbar-burger'), 0);

  // Check if there are any navbar burgers
  if ($navbarBurgers.length > 0) {

    // Add a click event on each of them
    $navbarBurgers.forEach( el => {
      el.addEventListener('click', () => {

        // Get the target from the "data-target" attribute
        const target = el.dataset.target;
        const $target = document.getElementById(target);

        // Toggle the "is-active" class on both the "navbar-burger" and the "navbar-menu"
        el.classList.toggle('is-active');
        $target.classList.toggle('is-active');

      });
    });
  }

});
</script>
